<template>
  <div class="book-container">
      <!-- {{$store.state.data.current_page}} -->
        <div class="main-menu-item editable" :class="{editing: $store.state.data.current_page === 'book_title'}" @click="setCurrentPage('book_title')">
            <i class="fas fa-edit"></i> Front Cover &amp; Title
        </div>
        <div class="main-menu-item editable" :class="{editing: $store.state.data.current_page === 'book_back'}" @click="setCurrentPage('book_back')">
            <i class="fas fa-edit"></i> Back Cover
        </div>
        <div class="main-menu-item">
            Chapters
        </div>
        <div class="chapters-container">
            <div class="single-chapter d-flex align-items-center" 
                :class="{added: chapter.added}" 
                v-for="chapter of $store.state.data.available_chapters" 
                :key="chapter.id" 
                :title="chapter.added 
                    ? 'This chapter is added to your book' 
                    : 'Open and edit this chapter to add it to your book.'"
                >
                <div class="title flex-grow-1">
                    {{ chapter.title }}
                </div>
                <div class="button">
                    <button class="btn btn-sm btn-secondary" v-if="!chapter.added" @click="addChapter(chapter.id)">Add</button>
                    <button class="btn btn-sm btn-primary" v-else @click="editChapter(chapter.id)">Edit</button>
                    <button class="btn btn-sm btn-danger" style="margin-left:5px" v-if="chapter.added" @click="deleteChapter(chapter.id)">Delete</button>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
export default {
    name: "Chapters",
    methods: {
        setCurrentPage(page){
            this.$store.commit('setCurrentPage', {current_page: page});
        },
        addChapter(chapter_id){
            // console.log(chapter_id);
            this.$emit('component_changed');
            this.$store.commit('addChapter', {chapter_id});
        },
        editChapter(chapter_id){
            // console.log(chapter_id);
            this.$emit('component_changed');
            this.$store.commit('editChapter', {chapter_id});
        },
        deleteChapter(chapter_id){
            this.$store.commit('deleteChapter', {chapter_id});
        },
        
    }
}
</script>

<style lang="scss" scoped>
.book-container {
    padding: 5px;
}
.chapters-container {
    padding: 5px;
}

.main-menu-item {
    padding: 10px;
    margin-bottom: 10px;
    font-size: 18px;

    &.editable {
        cursor:pointer;

        &:hover {
            box-shadow: 0 0 5px #ccc;
        }
    }

    &.editing {
        box-shadow: 0 0 5px #019be1;
        color: #019be1;
    }
}

.single-chapter {
    // box-shadow: 0 0 5px #ccc;
    margin-bottom: 10px;
    padding: 10px;

    &.added {
        color: black;
    }

    &:not(.added){
        color: #aaa;
    }
}
</style>